import axios from 'axios'
import store from '../store'

const base = store.getters.BASE + '/api/admin'

const setCommission = percent =>
  axios.post(base + '/set_commission', {
    commission: percent,
  })
const setWithdrawErrorTemplates = templates =>
  axios.post(base + '/withdraw_error_templates', {
    templates,
  })
const setCustomDepositError = error =>
  axios.post(base + '/set_deposit_error', {
    error,
  })
const setCustomWithdrawError = error =>
  axios.post(base + '/set_withdraw_error', {
    error,
  })
const setCustomDepositMin = (amount, currency) =>
  axios.post(base + '/set_min', {
    amount,
    currency,
  })
const requireConfirmationEmail = value =>
  axios.post(base + '/set_email_confirmation', {
    require_email: value,
  })

const getPromoList = () => axios.get(base + '/promo')
const createPromo = opts => axios.post(base + '/promo', opts)
const getTerms = () => axios.get(base + '/terms')
const getWithdrawErrorTemplates = () =>
  axios.get(base + '/withdraw_error_templates')
const setTerms = terms => axios.post(base + '/terms', { terms })

export {
  setCommission,
  setCustomDepositMin,
  setCustomDepositError,
  setWithdrawErrorTemplates,
  getWithdrawErrorTemplates,
  setCustomWithdrawError,
  requireConfirmationEmail,
  getPromoList,
  createPromo,
  getTerms,
  setTerms,
}
