const state = {
  user: {
    role: {
      name: 'owner',
    },
  },
  managers: [],
}

const mutations = {
  AUTH(state, user) {
    state.user = user
  },
  SET_MANAGERS(state, emails) {
    state.managers = emails
  },
}

const actions = {}

export default {
  state,
  mutations,
  actions,
}
