import axios from 'axios'
import store from '../store'

const base = store.getters.BASE + '/api/admin/'

const createContract = (title, pin, amount, symbol) =>
  axios.post(base + 'trade-guard/contract/create', {
    title,
    pin,
    amount,
    symbol,
  })

const getContracts = () => {
  return axios.get(base + 'trade-guard/contracts')
}

export { createContract, getContracts }
